.login {
    display: grid;
    height: 80vh;
    width: 100%;
    background-color: rgba(205, 232, 255, 0.85);
    align-items: center;
    justify-items: center;
}
.login .border {
    background-color: azure;
    /* text-align: center; */
    padding: 20px 10px;
    border-radius: 10px;
}
.login .border2 {
    /* display: grid; */
    /* height: 80vh; */
    /* width: 100%; */
    background-color: rgb(183, 221, 255);
    align-items: center;
    justify-items: center;
    padding: 20px 10px;
    border-radius: 10px;
}
.login .border2 p {
    text-align: center;
}
.errmessage {
    color: red;
    font-weight: bold;
}
.successmessage {
    color: green;
    font-weight: bold;
}
