h1 {
    color: black;
    margin: 5px 5px;
    padding: 10px;
    text-align: center;
    /* text-shadow: 0px 0px 5px rgb(255, 255, 255); */
}
/* .allCostomers {
    height: 100%;
    display: block;
} */
.table1 {
    background-color: rgb(225, 255, 245);
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    border-collapse: collapse;
    max-width: 100vw;
    min-width: min-content;
    /* margin: 5px; */
}
/* .allCostomers {
     max-width: 80em; 
} */
table .thead,
table .tfoot,
.tfoot {
    background-color: rgba(255, 255, 255, 0.9);
    text-align: center;
}
.table1 .thead {
    position: sticky;
    top: 3em;
}
.table1 .thead {
    border: solid 1px rgb(0, 8, 8);
}
td {
    padding: 0px 5px;
    border: solid 1px rgb(0, 8, 8);
}
tr {
    /* width: max-content; */
    /* max-width: max-content; */
    min-width: min-content;
}

.darkRow {
    background-color: rgba(0, 0, 0, 0.2);
}
.badRow {
    background-color: rgba(255, 0, 0, 0.5);
}
tbody tr:hover td {
    background-color: rgb(255, 255, 255);
}
/* tbody tr td {
    padding: 0px 5px;
    font-size: 1em;
    height: 20px;
} */
#edit {
    background-color: coral;
    padding: 0px;
    border: solid black 1px;
    text-align: center;
    margin: 0px auto;
    height: 80%;
    width: 1.2em;
}
#edit:hover {
    cursor: pointer;
    background-color: rgb(151, 0, 0);
}
@media print {
    .table1 .thead {
        position: relative;
    }
    td:first-child {
        display: none;
    }
}
.table1 .pagebrake {
    page-break-after: always;
}
