    display: inline-block;
    vertical-align: top;
    padding: 0px 10px;
} */
form .width4 {
    max-width: 5em;
}
form .width8 {
    width: 8em;
}
/* #sidebar div {
    position: sticky;
    top: 4em;
} */

form input[type="submit"],
.button1 {
    background-color: rgb(121, 121, 255);
    text-align: center;
    height: 2em;
    border-radius: 5px;
    max-width: fit-content;
    margin: 0px auto;
    border: 3px solid rgba(0, 28, 207, 0.453);
    color: white;
    margin: 3px 2px;
    box-sizing: border-box;
}

.button1 b {
    vertical-align: middle;
    /* padding-top: 10px; */
}
.oneCus form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2px;
    column-gap: 20px;
}
/* form div div {
    display: grid;
    grid-template-columns: 2fr 3fr;
} */
form input {
    border: none;
    background-color: rgb(234, 241, 215);
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 1em;
    height: 1.3em;
    max-width: 7em;
    border-radius: 2px;
    margin-bottom: 6px;
    border-bottom: #333 solid 2px;
    border-top: #333 solid 2px;
    padding: 3px 5px;
    color: black;
}
input:hover,
input:focus {
    background-color: rgb(255, 255, 255);
}
form input:invalid {
    background-color: rgba(165, 42, 42, 0.4);
    border: rgb(165, 0, 0) 1px dashed;
    /* max-width: calc(7em - 2px); */
}
form input[type="submit"]:hover,
.button1:hover {
    background-color: rgb(32, 32, 255);
    font-size: 15px;
    /* box-sizing: border-box; */
    /* color: black; */
    cursor: pointer;
    /* vertical-align: top; */
    margin: -0% -1px;
    margin-left: -1px;
    margin-top: -1px;
}

h1 a.button1:hover {
    font-size: inherit;
}
form input:disabled,
form input[type="submit"]:disabled,
a.button1:disabled,
.button1:disabled {
    background-color: rgb(146, 146, 150) !important;
    color: black;
    cursor: progress;
}
form input[type="submit"]:disabled:hover {
    background-color: red !important;
    /* color: black; */
    cursor: progress;
}
form input[type="date"] {
    font-size: 80%;
    max-width: 6rem;
}
.jobstatus div {
    display: inline-block;
    /* margin-right: 1em; */
}
.normal_input {
    background-color: rgba(0, 128, 0, 0.394);
}
.warnning_input {
    background-color: rgba(255, 0, 0, 0.3);
}
