/* right: 0px; */
.clear,
#workspace section .clear {
    position: absolute;
    padding: 0px 5px 4px;
    right: 0px;
    top: -0.8rem;
    font-size: 1.2em;
    font-weight: 900;
    color: red !important;
    background-color: rgb(255, 255, 255);
    /* float: right; */
    z-index: 10;
    /* margin-top: -1.1em;
    margin-left: -1.3em; */
    /* width: 1em;
    padding: 0px 2px 3px 8px; */
    /* border: 1px solid black; */
    border-radius: 5px;
}
#workspace section .clear:hover,
.clear:hover {
    background-color: red;
    color: azure !important;
}
/* .min-content {
    width: fit-content;
} */
form label {
    font-style: italic;
    font-size: 90%;
    font-weight: bold;
    color: black;
}
form input::placeholder {
    font-weight: 400;
}
.stickyTop {
    position: sticky;
    top: 4em;
    background-color: rgba(66, 66, 66, 0.8);
}
.fixedBottomLeft {
    /* position: absolute; */
    /* bottom: -75vh; */
    /* top: 70vh; */
    width: 100%;
    /* bottom: 2px; */
    /* left: 3px; */
    /* z-index: 2; */
    /* width: 215px; */
}
.mobileContainer {
    display: block;
    height: 100%;
}

.mobileButton {
    display: block;
    background-color: azure;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 1fr;

    /* width: 16em; */
    padding: 0px;
    border-radius: 10px;
    z-index: 10;
}
.mobileButton2 {
    display: block;
    background-color: azure;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr;

    /* width: 16em; */
    padding: 0px;
    border-radius: 10px;
    z-index: 10;
}
.searchoff,
.joboff {
    display: none;
}
.b-image {
    position: relative;
    right: 1rem;
    height: 2rem;
    vertical-align: middle;
    margin-bottom: 5px;
    margin-right: -2rem;
    border: green 3px solid;
    background-color: green 3px solid;
    border-radius: 1rem;
    box-sizing: border-box;
    /* border-image: fill; */
    /* width: 0px; */
}
.b-image:hover {
    border: rgb(128, 0, 0) 3px solid;
    background-color: rgb(128, 0, 0) 3px solid;
}
.icon-image {
    /* position: relative;
    right: 1rem; */
    height: 2rem;
    vertical-align: middle;
    margin-bottom: 5px;
    min-width: 3rem;
}
.icon-svg {
    /* height: 2rem; */
    cursor: pointer;
    vertical-align: middle;
    /* margin-bottom: 5px; */
    /* min-width: 3rem; */
    width: fit-content;
    height: 2rem;
    /* padding: -18px -7px;
    margin: -18px -7px; */
    border: none;
    background-color: rgb(121, 121, 255);
    border-radius: 5px;
}
.icon-svg2 {
    height: 2rem;
    /* width: 2.5rem; */
    cursor: pointer;
}
button.svg {
    cursor: pointer;
    border: none;
    box-sizing: border-box;

    width: min-content;
    height: min-content;
}
.icon-svg:hover {
    background-color: rgb(32, 32, 255);
}
/* button.svg:disabled, */
button.svg:disabled .icon-svg,
button.svg:disabled .icon-svg:hover {
    cursor: progress;
    border: none;
    box-sizing: border-box;
    background-color: rgb(134, 134, 143);
    width: min-content;
    height: min-content;
}
.icon-svg-exit {
    background-color: rgb(255, 140, 140);
    position: absolute;
    right: 0;
    top: -0.6rem;
    z-index: 10;
    height: 1.4rem;
    border-radius: 3px;
}
.icon-svg-clear {
    /* background-color: #9acd32; */
    border-radius: 8px;
    height: 1rem;
    position: inherit;
    /* right: 0; */
    /* top: 0rem; */
    margin-bottom: -0.2rem;
}
.icon-svg-exit:hover {
    background-color: red;
}
.icon-svg-clear:hover {
    background-color: red;
}
.grid2 {
    display: grid;
    grid-template-columns: auto auto;
}
.select {
    display: grid;
    grid-template-columns: auto auto auto;
    /* width: 10rem; */
    background-color: rgb(0, 126, 126);
    color: azure;
    /* padding: 3px; */
    z-index: 10;
    position: static;
    width: min-content;
    vertical-align: baseline;
    /* float: left; */
    margin-right: -200px;
    margin-top: -55px;
    position: absolute;
    bottom: 60px;
    /* vertical-align: top; */
    /* bottom: 25%; */
}
.select div {
    background-color: aquamarine;
    padding: 0px;
    margin: 1px;
    color: black;
    font-size: 80%;
    font-weight: 700;
    cursor: pointer;
    box-sizing: border-box;
    border: solid 2px rgba(255, 255, 255, 0);
    border-radius: 2px;
}
.select div:hover {
    border-color: rgb(255, 251, 0);
    border-radius: 5px;
}
.offscreen {
    position: absolute;
    top: 0vh;
    display: none;
}
@media (max-width: 900px) {
    /* .screen1000 {
        display: none;
    } */

    #home {
        display: grid;
        grid-template-columns: 1fr;
    }
    .garage {
        grid-row: span 2;
        width: 2em;
        display: grid;
        align-items: center;
        justify-items: center;
    }
    .garage p {
        display: none;
    }
    .garage span {
        display: inline;
    }
    /* #sidebar .stickyTop {
        display: grid;
        width: 100vw;
        height: 92vh;

        grid-template-columns: 2fr 1fr;
        position: fixed;
        left: 0px;
        z-index: 5;
    } */
    .mobileContainer {
        background-color: rgba(131, 129, 129, 0.7);
        display: grid;

        grid-template-columns: 12rem auto;
        position: fixed;
        width: 100%;
        /* height: 92vh; */
        left: 0px;
        z-index: 15;
    }
    .fixedBottomLeft {
        /* display: inline; */
        /* position: relative; */
        /* bottom: 0px; */
        position: sticky;
        top: 4rem;
        /* width: 66vw; */
        /* bottom: 3rem; */
        /* vertical-align: bottom; */
    }
    .mobileButton {
        background-color: azure;
        display: grid;
        justify-items: center;
        grid-template-columns: 1fr 1fr;
        position: fixed;
        bottom: 2px;
        left: calc(50vw - 8em);
        width: 16em;
        padding: 5px;
        border-radius: 10px;
        z-index: 10;
        font-size: large;
    }
    .mobileButton2 {
        /* background-color: rgba(173, 173, 173, 0.856); */
        display: grid;
        justify-items: center;
        grid-template-columns: 1fr;
        position: fixed;
        bottom: 2px;
        left: calc(50vw - 8em);
        width: 16em;
        padding: 2px;
        border-radius: 10px;
        z-index: 10;
    }

    .sidebaroff,
    .searchoff,
    .joboff {
        display: none;
    }
    body {
        font-size: 80%;
    }
}
form input .bg-red,
form input:hover.bg-red,
form input:focus.bg-red {
    background-color: rgba(255, 0, 0, 0.5) !important ;
}
