:root {
    --gap: 1px;
    --hhheader: 90px;
    --wwside: 200px;
}
#root {
    padding: 0px;
    margin: 0px;
    font-size: 80%;
}
body {
    /* background-color: rgba(84, 119, 90, 0.178); */
    /* margin: 2px; */
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    padding: 0px;
    /* zoom: calc(100vw / 15); */
}
.pageContainer {
    display: block;
    /* height: 99vh; */
    width: 100vw;
    box-sizing: border-box;
    /* zoom: 80%; */
    /* scale: 80%; */
    /* size: 80%; */
}
header,
footer {
    margin: 0px;
}
.bodyy {
    background-image: url("/public/img/bgcar.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    /* width: 100vw; */
    min-height: 90vh;
}
.middle {
    padding-bottom: 5rem;
    border-top: 5px solid red;
    /* min-height: calc(95vh - (40px + var(--hhheader))); */
    /* display: grid; */
    /* grid-template-columns: 200px calc(100% - 210px); */

    /* grid-template-columns: 1fr; */
    /* justify-items: center; */
    /* background-color: rgba(51, 51, 51, 0.575); */
    /* min-height: calc(100vh - 10em); */
}
.content {
    /* background-color: rgba(0, 255, 0, 0.5); */
    padding: 0px;
    height: 100%;
    overflow-y: scroll;
    /* overflow-y: scroll; */
}
.bb {
    font-weight: bolder;
}
.header {
    /* grid-column: span 2 / auto; */
    /* height: 4em; */
    /* background-color: rgb(92, 92, 92); */
    background-image: url("/public/img/bgcar3.jpg");

    background-size: cover;
    /* background-repeat: no-repeat; */
    /* width: 100vw; */
    display: grid;
    /* grid-template-columns: 1fr; */
    /* z-index: 8; */
}
footer.header {
    position: fixed;
    bottom: 0px;
    width: 100%;
}
.headerul {
    display: grid;
    grid-template-columns: min-content auto min-content;
    /* width: 100%; */
    height: 100%;
    /* background-color: ; */
    /* background-color: rgba(51, 51, 51, 0.5); */
    /* position: sticky;
    top: 0px; */
}
.headerull {
    background-color: rgba(51, 51, 51, 0.2);
    margin: 0;
    padding: 0;
    /* overflow: hidden; */
    list-style-type: none;
    box-sizing: border-box;
}
.garage {
    grid-row: span 2;
    display: grid;
    align-items: center;
    justify-items: center;
    text-align: center;
    color: aliceblue;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 1.2em;
    width: 150px;
    background-color: rgb(59, 153, 122);
    border: solid white 2px;
    box-sizing: border-box;
    text-shadow: 3px 5px 5px #333;
}
.garage p {
    /* padding-top: 0px; */
    /* margin: 0px; */
    margin-top: -2px;
    margin-bottom: 2px;
}
.garage span {
    display: none;
}
.headerull li {
    display: inline;
    float: left;
    min-width: 6em;
    /* backdrop-filter: blur(5px); */
}
.headerull a {
    background-color: rgba(51, 51, 51, 0.7);
    margin-right: 5px;
    /* border-right: solid 2px rgb(255, 255, 255); */
    display: block;
    text-decoration: none;
    color: aliceblue;
    font-weight: 700;
    font-size: 1.2em;
    /* padding: 2px 5px; */
    text-align: center;
    height: 1.5em;
}
.headerull a:hover {
    background-color: rgba(201, 207, 169, 0.659);
    /* color: black; */
    border-top-left-radius: 5px;
    border-top-right-radius: 40px;
}
.headerulll li:hover {
    background-color: rgba(135, 216, 88, 0.98);
    color: black;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.headerulll {
    margin: 0;
    padding: 0;
    padding-top: 3px;
    list-style-type: none;
    background-color: rgba(178, 207, 169);
    z-index: 5;
    border-bottom: rgba(255, 0, 0, 0.171) solid 5px;
}
.headerulll li {
    border-left: #333 1px solid;
    display: inline;
    float: left;
    /* border-right: solid 2px black; */
    /* min-width: 5em; */
    text-align: center;
    margin: 0px 2px;
    border-bottom: #458a30 solid 5px;
}
.headerulll a {
    margin-right: 5px;
    min-width: 5em;
    background-color: none;
    color: black;
    text-decoration: none;
    font-weight: 700;
    /* padding: 2px 5px; */
    height: 1.5em;
    vertical-align: middle;
    padding: 2px 5px;
}
