    background-color: rgb(178, 207, 169);
    color: black;
    border-top-left-radius: 5px;
    border-top-right-radius: 40px;
} */
/* .headerulll  */
li #active {
    background-color: #66cc47;
    color: black;
    /* border-top-left-radius: 10px; */
    border-top-right-radius: 25px;
    border-bottom: #458a30 solid 5px;
}

li#active2 {
    background-color: #66cc47;
    color: black;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom: #458a30 solid 5px;
}
.nav {
    background-color: rgb(128, 240, 202);
    padding: 0px 0px;
    border-radius: 3px;
    border: solid black 1px;
    text-align: center;
}
.search {
    display: grid;
    grid-template-columns: auto auto;
    gap: 10px;
}
.search section {
    background-color: rgb(128, 170, 160);
    align-items: center;
    /* align-content: center; */
    text-align: center;
}
.simpleSearch {
    display: inline-block;
    /* border-radius: 5px; */
    border: black solid 2px;
    width: 100%;
    min-width: 10em;
    text-align: center;
    /* background-color: rgb(114, 191, 194);*/
    background-color: rgba(131, 129, 129, 0.7);
    box-sizing: border-box;
}
.paddingT1 {
    padding: 5px 3px;
    text-align: center;
    background-color: azure;
}
.paddingT1 div {
    padding: 5px 0px;
}
.searchBox {
    padding: 5px;
    display: block;
    width: 100%;
    margin: 0px 0px;
    background-color: rgba(109, 124, 103, 0.95);
    border-bottom: solid 2px white;
    border-radius: 0px;
    box-sizing: border-box;
    text-align: left;
}
.searchBox .paddingT1 {
    color: white;
    font-size: small;
    margin-bottom: 5px;
    text-align: center;
}
.searchBox input {
    height: 25px;
    border: none;
    box-sizing: border-box;
    padding-left: 5px;
    border-radius: 0px;
    font-size: 15px;
    font-weight: bolder;
    box-sizing: border-box;
    width: 95%;
    margin-right: -1.5em;
    border-radius: 10px;
    margin-left: -1.5em;
}
.searchText {
    text-align: center;
}
.searchText span {
    padding: 0px 5px;
    padding-bottom: 2px;
    font-size: 1em;
    font-weight: bold;
    border-radius: 10px;
    margin-left: -1em;
    color: rgb(231, 124, 124);
}
.searchText:hover span {
    color: red;
}
.searchText span:hover {
    background-color: red;
    cursor: pointer;
    color: black;
}
#resultListContainer {
    z-index: 2;
    position: absolute;
    top: 7em;
    margin-top: 1px;
    width: 100%;
    background-color: rgba(137, 155, 129, 0.95);
    box-sizing: border-box;
    border-radius: 3px;
    border: solid 2px black;
    border-left: solid 10px rgb(0, 0, 0);
    text-align: left;
}
.fixedBottomLeft #resultListContainer {
    z-index: 2;
    position: inherit;
    /* top: 7em; */
    margin-top: 1px;
    width: 100%;
    background-color: rgba(137, 155, 129, 0.95);
    box-sizing: border-box;
    border-radius: 3px;
    border: solid 2px black;
    border-left: solid 10px rgb(0, 0, 0);
    text-align: left;
}
.headerul #resultListContainer {
    position: absolute;
}
#resultListContainer a {
    text-decoration: none;
    color: rgb(0, 0, 0);
}
.resultList {
    font-weight: bold;
    margin: 0px;
    padding: 1px 10px;
    border-bottom: solid 2px rgb(82, 82, 82);
}
.resultList:hover {
    border-radius: 10px;
    background-color: #333;
    margin: 0px;
    width: 100%;
    box-sizing: border-box;
    color: white;
}
.findType {
    padding: 0px 5px;
    border: solid 1px rgb(0, 70, 6);
    /* border-radius: 3px; */
    margin: 0px 2px;
    cursor: pointer;
}
.findTypeActive {
    padding: 0px 5px;
    border: solid 1px rgb(0, 70, 6);
    /* border-radius: 3px; */
    margin: 0px 2px;
    background-color: rgb(27, 172, 22);
    cursor: pointer;
}
.findType:hover {
    background-color: brown;
    border: solid 1px rgb(95, 24, 24);
    cursor: pointer;
    /* font-size: 80%; */
}
.oneCus {
    display: grid;
    grid-template-rows: auto;
    align-items: center;
    justify-items: center;
    width: fit-content;
    min-width: 21em;
    margin: auto;
    padding: 10px 10px;
    background-color: aliceblue;
    border-radius: 15px;
    border: solid greenyellow 5px;
}
.noname {
    display: grid;
    grid-template-rows: auto;
    align-items: center;
    justify-items: center;
    width: calc(100vw - 20px);
    height: calc(100vh - 120px);
}
.oneCusDisp {
    height: calc(100% - 90px);
    position: absolute;
    top: 90px;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.774);
    display: block;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    box-sizing: border-box;
}
.oneCusDisp button {
    background-color: rgb(255, 0, 0);

    padding: 5px 10px;
    border-radius: 10px;
    width: 150px;
    margin: 10px;
    color: rgb(0, 0, 0);
    font-weight: 600;
    font-size: 1.5em;
    border: solid white 3px;
}
.green button {
    background-color: greenyellow;
}
.oneCusDisp button:hover {
    background-color: rgb(230, 130, 0);
}
.exit {
    width: 150px;
    margin: 0px auto;
}
.oneCusDisp text {
    color: blue;
}

.pointer:hover {
    cursor: pointer;
}
.hoverRed:hover {
    cursor: pointer;
    background-color: red;
}
.carbg1 {
    position: sticky;
    height: 0px;
    top: 0px;
    width: 100%;
    /* margin-right: ; */
    z-index: -10;
    filter: blur(1px);
}
.carbg1 img {
    width: 100%;

    overflow: hidden;
}
.green {
    background-color: green;
    color: aliceblue;
    padding-left: 5px;
    padding-right: 5px;
    font-weight: bold;
}
.red {
    background-color: rgba(255, 0, 0, 0.583);
    color: rgb(0, 0, 0);
    padding-left: 5px;
    padding-right: 5px;
    font-weight: bold;
}
.inline {
    display: inline-block;
    /* vertical-align: top; */
    padding: 0px 5px;
}
h1 {
    background-color: rgba(240, 248, 255, 0.8);
}
.total {
    background-color: beige;
    text-align: center;
    border: #333 solid 2px;
    /* max-width: min-content; */
    /* min-width: 100vw; */
    width: 100vw;
    /* max-width: 95vw; */
}
.total div {
    margin: 2px;
}
.total input {
    margin: 2px;
}
@media print {
    .header {
        display: none !important;
    }
    .bodyy {
        background-image: none;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 100%;
        height: 100vh;
        background-color: white;
    }
    .middle {
        background-color: white;
    }
}
.off {
    display: none;
}
.bg-red {
    background-color: rgba(255, 0, 0, 0.5);
}
